.form-main-container{
    width:100vmin;
    display: flex;
    flex-direction: column;
    justify-content: center;
    justify-content: space-evenly;
    
}

.form-main-container form{
    display: flex;
    justify-content: end;
    margin-right:0.80rem;
}

.form-main-container :focus{
    border: none;

    box-shadow: 0 0 0 1pt grey;
    outline: none;
    transition: .1s;
}


.form-main-container button {
    border-style: none;
    margin-left: 0.40rem;
    border-radius: 10px;
    height: 2rem;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    cursor: pointer;
}

.form-main-container button:hover {
   opacity: 0.5;
   transition: .1s ease-in ;
}

.form-main-container button:focus {

    border: none;
  
    box-shadow: 0 0 0 1pt grey;
    outline: none;
    transition: .1s;
  
}

.form-input-field{
    border-radius: 15px;
    border-style: none;
    height: 2rem;
    width:180px;
}

